@import "./src/config/AppDesign";

.homepage-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  position: relative;

  > img {
    position: absolute;
    border-radius: 50vw;
    opacity: 0.4;
    animation: fadeIn ease-in 2s;
  }

  > h1,
  p {
    font-family: Geneva-Regular, sans-serif;
    color: white;
    z-index: 1;
    animation: fadeIn ease-in 2s;
    text-align: center;
  }

  > a {
    text-decoration: none;
    z-index: 1;
    transition: 0.6s;

    .discord-icon {
      width: 5rem;
      object-fit: contain;
      transition: 0.6s;
      opacity: 0.4;

      &:hover {
        width: 6rem;
        transition: 0.6s;
        opacity: 1.0;
      }

      &:active {
        width: 6rem;
        opacity: 0.8;
        transition: 0.3s;
      }
    }
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.4;
  }
}

@-moz-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.4;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.4;
  }
}

@-o-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.4;
  }
}

@-ms-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.4;
  }
}
